<template>
  <v-app id="inspire">
    <v-content>
      <v-container
        class="fill-height"
        fluid
      >
        <v-row
          align="center"
          justify="center"
        >
          <v-col
            cols="12"
            sm="8"
            md="4"
          >
            <v-card class="elevation-12">
              <v-toolbar
                color="primary"
                dark
                flat
              >
                <v-toolbar-title>Login form</v-toolbar-title>
                <v-spacer></v-spacer>
               
              </v-toolbar>
              <v-card-text>
                <v-form  v-model="valid" ref="form">
                  <v-text-field
                     v-model="username"
                    label="Username"
                    name="login"
                    autocomplete="username"
                    :rules="rules.username"
                    prepend-icon="mdi-account"
                    type="text"
                    required
                    @keypress.enter="submit"
                  ></v-text-field>

                  <v-text-field
                    v-model="password"
                    label="Password"
                    name="password"
                    autocomplete="current-password"
                    prepend-icon="mdi-lock"
                    type="password"
                    :rules="rules.password"
                    required
                    @keypress.enter="submit"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-btn color="green" @click="submit($event, true)" :disabled="!valid">register</v-btn>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="submit" :disabled="!valid" >Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>
<script>
  import { mapActions } from 'vuex'
  export default {
    name: 'Login',
    data() {
      return {
        valid: false,
        username: '',
        password: '',
        rules: {
          username: [v => !!v || 'Username is required', v => v.length >= 5 || 'Username must be more or equal to 5 characters'],
          password: [v => !!v || 'Password is required', v => v.length >= 6 || 'Password must be more or equal to 6 characters']
        }
        
      }
    },
    methods: {
      ...mapActions({
        register: 'auth/register',
        login: 'auth/login'
      }),
       submit(evt, register = false) {
        evt.preventDefault()

        if (this.$refs.form.validate()) {
          const { username, password } = this
          let fn = register ?  this.register( { username, password } ) :  this.login( { username, password } );
          fn.catch(err=> {
            this.$store.commit('error', err.response.data)
          })
        } else {
            this.$store.commit('error', 'INVALID FORM PLEASE CHECK THE FORM AND TRY AGAIN!')
        }
      
      } 
    }
  }
</script>